<template>
  <va-card :title="$t('tables.labelAttendance')" class="w-100">
    <div class="row mr-0">
      <div class="flex xs12 sm4">
        <label class="input-label"> {{$t('forms.search')}} </label>
        <va-input v-model="searchText" @input="searchDelay" removable :placeholder="$t('forms.search')" @keyup.enter="search"  />
      </div>
      <!-- <va-button
        icon="fa fa-search"
        flat
        slot="actions"
        color="success"
        class="mr-0"
        @click="search"
      />
      <va-button
        icon="fa fa-trash"
        flat
        slot="actions"
        color="danger"
        class="mr-0"
        @click="deletesearch"
      /> -->

    </div>
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      :no-pagination = "searchBool"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        <va-icon name="fa fa-circle" :color="props.rowData.color" size="8px" />
      </template>
      <template slot="date" slot-scope="props">
        {{ formatDate(props.rowData.date)}}
      </template>
      <template slot="iS_Vacation" slot-scope="props">
        {{props.rowData.iS_Vacation  ? '&#10004;': '&#10008;' }}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

  </va-card>

</template>

<script>
import AttendanceService from '../../../../services/BasicData/AttendanceService'
import { debounce } from 'lodash'

export default {
  data () {
    return {
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {},
      searchText: '',
      searchBool: false,
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
      }, {
        name: '__slot:date',
        title: this.$t('tables.headings.date'),
        sortField: 'date',
        sortCallBack: 'sortCallBack|date',

      }, {
        name: 'total_Attendance',
        title: this.$t('tables.headings.total_Attendance'),
        sortField: 'total_Attendance',
        sortCallBack: 'sortCallBack|total_Attendance',
      }, {
        name: 'total_Absence',
        title: this.$t('tables.headings.total_Absence'),
        sortField: 'total_Absence',
        sortCallBack: 'sortCallBack|total_Absence',
      }, {
        name: '__slot:iS_Vacation',
        title: this.$t('tables.headings.iS_Vacation'),

      }]
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    readItems (page = 1, orderBy = null) {
      this.loading = true
      this.pageNumber = page

      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        OrderBy: orderBy,
      }
      AttendanceService.getAllPagnation(params)
        .then(response => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    sortCallBack (obj) {
      this.readItems(1, (obj[0].toString().charAt(0).toUpperCase() + obj[0].toString().slice(1)))
    },
    formatDate (dateString, FullDate = true) {
      if (FullDate) {
        const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      } else {
        const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      }
    },
    searchDelay: debounce(function (searchBarCodeTerm) {
      this.search()
    }, 800),

    search () {
      this.loading = true
      if (this.searchText === '') {
        return this.readItems()
      }

      const params = {
        Text: this.searchText,
      }
      this.dataTable = []
      AttendanceService.search(params)
        .then(response => {
          this.dataTable = response.data
          this.totalPages = Math.ceil(this.dataTable.length / 10)
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },

    deletesearch () {
      this.searchText = ''
      this.searchBool = false
      this.readItems()
    },
  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
